export interface InvoiceFormModalProps {
    locationId: string,
    storeId?: string,
    manualRefresh: any
}
export const InvoiceFormModal: FC<InvoiceFormModalProps> = observer(function UpsertFormModal(_props) {
    const [modalVisible, setModalVisible] = React.useState(false);
    const [selected, setSelected] = useState(undefined);
    
    const dataYears = [
        { label: '2023', value: '2023' },
        { label: '2024', value: '2024' },
        { label: '2025', value: '2025' },
        { label: '2026', value: '2026' },
        { label: '2027', value: '2027' },
    ];
    const dataMonths = [
        { label: 'January', value: '1' },
        { label: 'February', value: '2' },
        { label: 'March', value: '3' },
        { label: 'April', value: '4' },
        { label: 'May', value: '5' },
        { label: 'June', value: '6' },
        { label: 'July', value: '7' },
        { label: 'August', value: '8' },
        { label: 'September', value: '9' },
        { label: 'October', value: '10' },
        { label: 'November', value: '11' },
        { label: 'December', value: '12' },
    ];
    const dataDates = (() => {
      let dates = []
      for (let i = 1; i <= 31; i++) {
        dates.push({ label: i.toString(), value: i.toString() })
      }
      return dates
    })();
    const {
      invoiceStore,
      delnoteStore: deliverynoteStore,
      authenticationStore: { authToken, logout },
    } = useStores()

    const { manualRefresh } = _props;

    const {
      setPageIndex,
      setPageCount,
      fetchDelnotes,
      // invoice form
      setInvoiceFormModalVisible,
      invoiceFormModalVisible,
      setInvoiceFormId,
      invoiceFormId,
      invoiceFormStoreId,
      setInvoiceFormStoreId,
      invoiceFormLocationId,
      setInvoiceFormLocationId,
      invoiceFormYear,
      setInvoiceFormYear,
      invoiceFormMonth,
      setInvoiceFormMonth,
      invoiceFormSubmitInprogress,
      invoiceFormSubmitErrorMessage,
      invoiceFormSubmitSuccessMessage,
      invoiceFormSubmitMessage,
      setInvoiceFormSubmitInprogress,
      setInvoiceFormSubmitErrorMessage,
      setInvoiceFormSubmitSuccessMessage,
      setInvoiceFormSubmitMessage,
      invoiceFormStoreComboboxOptions,
      invoiceFormLocationComboboxOptions,
      fetchInvoiceFormLocations,
      fetchInvoiceFormStores,
      submitInvoiceForm,
      invoiceFormIsCreateNew,
      setInvoiceFormIsCreateNew,
      fetchInvoiceFormInvoices,
      invoiceFormInvoicesComboboxOptions
    } = deliverynoteStore

    const { reloadInvoices } = invoiceStore;

    async function submitForm() {
        console.log('submitForm')
        setInvoiceFormSubmitInprogress(true)
        setInvoiceFormSubmitMessage("")
        setInvoiceFormSubmitErrorMessage("")
        setInvoiceFormSubmitSuccessMessage("")
        await submitInvoiceForm(decryptAccessToken(authToken), reloadInvoices, logout)
        // await fetchDelnotes(decryptAccessToken(authToken), 0)
        // if(invoiceFormIsCreateNew){
        //   await fetchInvoiceFormInvoices(decryptAccessToken(authToken))
        // }

        // await new Promise(resolve => setTimeout(resolve, 4000))
        // setInvoiceFormSubmitInprogress(false)
        // setInvoiceFormSubmitSuccessMessage("Success Create Delivery Note")
        // setDeliverynotes([])
        // fetchDelnotes(decryptAccessToken(authToken))
        // setPageSize(6)
        // setPageIndex(0)
        // setPageCount(0)
        // await fetchDelnotes(decryptAccessToken(authToken))

        // setModalVisible(!modalVisible)
    }
    async function resetForm(){
      console.log('resetForm')
      setInvoiceFormLocationId("")
      await fetchInvoiceFormStores(decryptAccessToken(authToken))
      setInvoiceFormStoreId("")
      setInvoiceFormYear("")
      setInvoiceFormMonth("")
      setInvoiceFormSubmitInprogress(false)
      setInvoiceFormSubmitMessage("")
      setInvoiceFormSubmitErrorMessage("")
      setInvoiceFormSubmitSuccessMessage("")
      setInvoiceFormId("")
      setInvoiceFormIsCreateNew(true)
    }
    async function onSelectLocation(location){
        console.log('onSelectLocation', location.value)
        setInvoiceFormLocationId(location.value)
        await fetchInvoiceFormStores(decryptAccessToken(authToken))
    }
    
    async function onSelectStore(store){
      setInvoiceFormStoreId(store.value)
    }
    async function onSelectYear(year){
      setInvoiceFormYear(year.value)
    }
    async function onSelectMonth(month){
      setInvoiceFormMonth(month.value)
    }
    async function onSelectInvoice(invoice){
      setInvoiceFormId(invoice.value)
    }
    useEffect(() => {
      ;(async function load() {
        // setIsLoading(true)
        await fetchInvoiceFormLocations(decryptAccessToken(authToken))
        await fetchInvoiceFormStores(decryptAccessToken(authToken))
        // setIsLoading(false)
      })()
    }, [deliverynoteStore])

    useEffect(() => {
      ;(async function load() {
        if(invoiceFormLocationId && invoiceFormStoreId && invoiceFormYear && invoiceFormMonth){
          console.log('execute fetchInvoiceFormInvoices')
          await fetchInvoiceFormInvoices(decryptAccessToken(authToken))
        }
      })()
    }, [invoiceFormLocationId,invoiceFormStoreId,invoiceFormYear,invoiceFormMonth])
    
    
    
    return (
        <View style={styles.centeredView}>
          <Modal 
                visible={invoiceFormModalVisible}
                onRequestClose={() => {setInvoiceFormModalVisible(false)}}
                onPressClose={() => {setInvoiceFormModalVisible(false)}}
            >
              <Text style={styles.modalText}>Invoice Form</Text>
              {invoiceFormSubmitInprogress && <Text style={{ color: colors.palette.primary600 }}>Inprogress...</Text>}
              {!!invoiceFormSubmitErrorMessage && <Text style={{ color: colors.error }}>
                {invoiceFormSubmitErrorMessage}
              </Text>}
              {!!invoiceFormSubmitSuccessMessage && <Text style={{ color: "green" }}>
                {invoiceFormSubmitSuccessMessage}
              </Text>}   
              <Dropdown
                  label="Location"
                  placeholder="Select Location"
                  data={invoiceFormLocationComboboxOptions}
                  onSelect={onSelectLocation}
                  containerStyle={$textField}
                  defaultValue={invoiceFormLocationId}
                  />
              <Dropdown
                  label="Store"
                  placeholder="Select Store"
                  data={invoiceFormStoreComboboxOptions}
                  onSelect={onSelectStore}
                  containerStyle={$textField}
                  defaultValue={invoiceFormStoreId}
                  />
              {/* <DatetimePicker /> */}
              <Dropdown
                  label="Year"
                  placeholder="Select Year"
                  data={dataYears}
                  onSelect={onSelectYear}
                  containerStyle={$textField}
                  defaultValue={invoiceFormYear}
                  />
              <Dropdown
                  label="Month"
                  placeholder="Select Month"
                  data={dataMonths}
                  onSelect={onSelectMonth}
                  containerStyle={$textField}
                  defaultValue={invoiceFormMonth}
                  />
              {(!!invoiceFormLocationId && !!invoiceFormStoreId && !!invoiceFormYear && !!invoiceFormMonth) && <View style={$toggle}>
                <Toggle
                  value={invoiceFormIsCreateNew}
                  onValueChange={() =>{
                    // episodeStore.setProp("favoritesOnly", !episodeStore.favoritesOnly)
                    let isNew = !invoiceFormIsCreateNew
                    if(isNew) setInvoiceFormId("")
                    setInvoiceFormIsCreateNew(!invoiceFormIsCreateNew)
                  }}
                  variant="switch"
                  labelTx={invoiceFormIsCreateNew ? "invoiceFormModal.switchCreateInvoice":"invoiceFormModal.switchUpdateInvoice"}
                  labelPosition="left"
                  labelStyle={[$labelStyle]}
                  accessibilityLabel={translate("invoiceFormModal.switchUpdateOrCreateInvoice")}
                />
              </View>}

              {!invoiceFormIsCreateNew && <Dropdown
                  label="Invoice"
                  placeholder="Select Existing Invoice"
                  data={invoiceFormInvoicesComboboxOptions}
                  onSelect={onSelectInvoice}
                  containerStyle={$textField}
                  defaultValue={invoiceFormId}
                  />}
              {/* <Dropdown
                  label="Date"
                  placeholder="Select Date"
                  data={dataDates}
                  onSelect={onSelectDate}
                  containerStyle={$textField}
                  defaultValue={invoiceFormDeliveryDate}
                  /> */}
              
              <View>
                <Pressable
                  disabled={invoiceFormSubmitInprogress}
                  style={[styles.button, invoiceFormSubmitInprogress ? styles.buttonDisable : styles.buttonClose]}
                  onPress={() => submitForm()}>
                  <Text style={styles.textStyle}>Submit</Text>
                </Pressable>
                <Pressable
                  style={[styles.button, styles.buttonClose, {marginTop: 10}]}
                  onPress={() => resetForm()}>
                  <Text style={styles.textStyle}>Reset</Text>
                </Pressable>
              </View>
            </Modal>
            <Pressable
                style={[styles.button, styles.buttonOpen]}
                onPress={() => setInvoiceFormModalVisible(true)}>
                <Text style={styles.textStyle}>Invoice Form</Text>
            </Pressable>

            {/* <View style={{flex: 1}}>
                <TextField
                    value={locationId}
                    onChangeText={setLocationId}
                    containerStyle={$textField}
                    autoCapitalize="none"
                    autoComplete="name"
                    autoCorrect={false}
                    keyboardType="default"
                    labelTx="loginScreen.emailFieldLabel"
                    placeholderTx="loginScreen.emailFieldPlaceholder"
                />
            </View> */}
        </View>
    )
})

const $labelStyle: TextStyle = {
  textAlign: "left",
}
const $textField: ViewStyle = {
    marginBottom: spacing.lg,
  }
const $toggle: ViewStyle = {
  marginBottom: spacing.md,
}

