const ICON_SIZE = 14

const rnrImage1 = require("app/../assets/images/rnr-image-1.png")
const rnrImage2 = require("app/../assets/images/rnr-image-2.png")
const rnrImage3 = require("app/../assets/images/rnr-image-3.png")
const rnrImages = [rnrImage1, rnrImage2, rnrImage3]

export const ReceiptScreen: FC<DemoTabScreenProps<"ReceiptScreen">> = observer(function ReceiptScreen(_props) {
  
  const { navigation } = _props

  // Pull in one of our MST stores
  const { receiptStore, authenticationStore: { authToken, hasPriv } } = useStores()
  
  const [refreshing, setRefreshing] = React.useState(false)
  const [isLoading, setIsLoading] = React.useState(false)

  // initially, kick off a background refresh without the refreshing UI
  useEffect(() => {
    ;(async function load() {
      setIsLoading(true)
      await receiptStore.fetchReceipts(decryptAccessToken(authToken), 0)
      setIsLoading(false)
    })()
  }, [receiptStore])

  // simulate a longer refresh, if the refresh is too fast for UX
  async function manualRefresh() {
    console.log('manualRefresh')
    // setRefreshing(true)
    // deliverynoteStore.setPageIndex(0)
    // await Promise.all([deliverynoteStore.fetchDelnotes(decryptAccessToken(authToken)), delay(750)])
    // setRefreshing(false)

    setRefreshing(true)
    await receiptStore.reloadReceipts(decryptAccessToken(authToken), 0)
    setRefreshing(false)
  }
  async function nextPage() {
    console.log('nextPage')
    setRefreshing(true)
    await delay(750)
    receiptStore.fetchReceipts(decryptAccessToken(authToken), receiptStore.pageIndex + 1)
    setRefreshing(false)
  }

  // Pull in navigation via hook
  // const navigation = useNavigation()
  if(!hasPriv('udgisi_admin')) return null
  return (
    <Screen
      preset="scroll"
      safeAreaEdges={["top"]}
      contentContainerStyle={$screenContentContainer}
    >
      {/* <Text style={$title} preset="heading" tx="deliverynoteScreen.title" /> */}
      <FlatList<Receipt>
        data={receiptStore.receiptsForList}
        extraData={receiptStore.receipts.length}
        contentContainerStyle={$flatListContentContainer}
        refreshing={refreshing}
        onRefresh={manualRefresh}
        onEndReachedThreshold={0.2}
        onEndReached={nextPage}
        ListEmptyComponent={
          isLoading ? (<ActivityIndicator />) : (
            <EmptyState
              preset="generic"
              style={$emptyState}
              // headingTx={
              //   episodeStore.favoritesOnly
              //     ? "demoPodcastListScreen.noFavoritesEmptyState.heading"
              //     : undefined
              // }
              headingTx={"demoPodcastListScreen.noFavoritesEmptyState.heading"}
              // contentTx={
              //   episodeStore.favoritesOnly
              //     ? "demoPodcastListScreen.noFavoritesEmptyState.content"
              //     : undefined
              // }
              contentTx={"demoPodcastListScreen.noFavoritesEmptyState.content"}
              // button={episodeStore.favoritesOnly ? null : undefined}
              buttonOnPress={manualRefresh}
              imageStyle={$emptyStateImage}
              ImageProps={{ resizeMode: "contain" }}
            />
          )
        }
        ListHeaderComponent={
          <View style={$heading}>
            <Text preset="heading" tx="receiptScreen.title" />
            <View style={{
              flex: 1,
              flexDirection: 'row',
              alignContent: 'space-between',
              backgroundColor: colors.palette.neutral300,
              borderRadius: 10,
              marginTop: 10,
            }}>
              <FilteringFormModal locationId="hallooooo" />
              <View style={[styles.centeredView, {marginTop: 0, padding: 5}]}>
                <Pressable
                  style={[styles.button, styles.buttonOpen]}
                  onPress={() => openLinkInBrowser(`${Config.API_URL}udgisi/print-receipt?number=${receiptStore.filterReceiptNumber}&year=${receiptStore.filterYear}&month=${receiptStore.filterMonth}`)}>
                  <Text style={styles.textStyle}>Print</Text>
                </Pressable>
              </View>
            </View>
          </View>
        }
        // ListFooterComponent={
        //   <View style={$heading}>
        //     <Text preset="heading" tx="deliverynoteScreen.title" />
        //   </View>
        // }
        renderItem={({ item }) => (
          <ItemCard
            key={item.id}
            receipt={item}
            // onPressFavorite={() => episodeStore.toggleFavorite(item)}
            onPressCard={async () => {
                receiptStore.setUpsertFormId(item.id);
                receiptStore.setUpsertFormReceiptNumber(item.nomor_kwitansi);
                receiptStore.setUpsertFormModalVisible(true);
            }}
            // onPressCard={() => navigation.navigate("deliverynoteDetail", { episode: item })}
          />
        )}
      />
      <UpsertFormModal hideButtonOpen={true} />
    </Screen>
  )
})

const ItemCard = observer(function ItemCard({
  receipt,
  onPressCard,
}: {
  receipt: Receipt
  onPressCard: () => void
}) {
  const imageUri = useMemo(() => {
    return rnrImages[Math.floor(Math.random() * rnrImages.length)]
  }, [])

  const handlePressCard = () => {
    // openLinkInBrowser(episode.enclosure.link)
    onPressCard()
  }

  
  return (
    <Card
      style={$item}
      verticalAlignment="force-footer-bottom"
      onPress={handlePressCard}
      onLongPress={null}
      HeadingComponent={
        <View style={$metadata}>
          <Text
            style={$metadataText}
            size="xxs"
          >
            {/* {`${delnote.storeLocationName} / ${delnote.deliveryYearMonth} / ${delnote.store.store_name}`} */}
            {/* {`${delnote.storeLocationName} / ${delnote.deliveryYearMonth} / ${delnote.store.store_name} / total case ${delnote.total_case}`} */}
          </Text>
          {/* <Text
            style={$metadataText}
            size="xxs"
            accessibilityLabel={'tesssting'}
            // accessibilityLabel={episode.duration.accessibilityLabel}
          >
            {episode.duration.textLabel}
          </Text> */}
        </View>
      }
      content={`${receipt.nomor_kwitansi}`}
      // {...accessibilityHintProps}
      // RightComponent={<Image source={imageUri} style={$itemThumbnail} />}
      FooterComponent={null}
    />
  )
})
