import { Instance, SnapshotIn, SnapshotOut, types } from "mobx-state-tree"
import { withSetPropAction } from "./helpers/withSetPropAction"
import { api } from "../services/api"
import { Delnote, DelnoteModel } from "./Delnote"
import { Store, StoreModel } from "./Store"
import { Invoice, InvoiceModel } from "./Invoice"
import { Receipt, ReceiptModel } from "./Receipt"
import { LocationModel } from "./Location"

/**
 * Model description here for TypeScript hints.
 */
export const InvoiceStoreModel = types
  .model("InvoiceStore")
  .props({
    invoices: types.array(InvoiceModel),
    receipts: types.optional(types.array(types.string), []),
    // filtering
    filterStores: types.array(StoreModel),
    filterLocations: types.array(LocationModel),
    filterLocationId: "",
    filterStoreId: "",
    filterInvoiceNumber: "",
    filterYear: "",
    filterMonth: "",
    filterModalVisible: false,

    // upsertForm
    upsertFormModalVisible: false,
    upsertFormId: "",
    upserteFormSubmitInprogress: false,
    upsertFormSubmitErrorMessage: "",
    upsertFormSubmitSuccessMessage: "",
    upsertFormInvoiceNumber: "",
    
    // receipt form
    receiptFormId: "",
    receiptFormReceipts: types.optional(types.array(ReceiptModel), []),
    receiptFormStores: types.array(StoreModel),
    receiptFormLocations: types.array(LocationModel),
    receiptFormLocationId: "",
    receiptFormStoreId: "",
    receiptFormYear: "",
    receiptFormMonth: "",
    receiptFormModalVisible: false,
    receiptFormIsCreateNew: true,
    receiptFormSubmitInprogress: false,
    receiptFormSubmitMessage: "",
    receiptFormSubmitErrorMessage: "",
    receiptFormSubmitSuccessMessage: "",
    // pagination
    pageIndex: 0,
    pageSize: 6,
    pageCount: 0,
    column: "id,nomor_invoice,receipt{id,nomor_kwitansi}"
  })
  .actions(withSetPropAction)
  .actions((store) => ({
    setUpsertFormInvoiceNumber(value?: string) { store.upsertFormInvoiceNumber = value },
    setUpserteFormSubmitInprogress(value?: boolean) { store.upserteFormSubmitInprogress = value },
    setUpsertFormModalVisible(value?: boolean) {
        store.upsertFormModalVisible = value;
    },
    setUpsertFormId(value?: string) { store.upsertFormId = value },
  }))
  .actions((store) => ({
    setPageIndex(value?: number) {
        store.pageIndex = value
    },
    setPageSize(value?: number) {
        store.pageSize = value
    },
    setPageCount(value?: number) {
        store.pageCount = value
    },
    setInvoices(value?: any) {
        store.invoices = value
    },
    toggleReceiptReset() {
        store.receipts.clear()
    },
    async fetchReceiptFormReceipts(authToken: string) {
        const serviceName = 'getAllTbKwitansi'
        // console.log('before query')
        const query = `query{${serviceName}(request:{page_size: 10000, page_index:0, filter: {${store.receiptFormMonth? 'receipt_month:'+store.receiptFormMonth+',' : ''}${store.receiptFormYear? 'receipt_year:'+store.receiptFormYear+',' : ''}}}){ status,error,count,page_count,list_data{id,nomor_kwitansi}}}`
        // console.log('after query ', query)
        let response = await api.graphqlReceipt(query, authToken)
        // console.log('query done', response)
        if (response.kind === "ok") {
          // console.log('create form stores==>', response.stores)
          store.setProp("receiptFormReceipts", response.receipts)
        } else {
          // console.log('create form stores gagal==>', response.stores)
          console.tron.error(`Error fetching locations: ${JSON.stringify(response)}`, [])
        }
      },
    async fetchInvoices(authToken: string, pageIndex: number) {
        store.setProp("pageIndex", pageIndex)
        if(pageIndex > 0 && store.pageCount <= pageIndex){
        }else{
            if(pageIndex == 0) store.setProp("pageCount", 0)
            store.setProp("pageCount", 0)
            const serviceName = 'getAllTbInvoice'
            let query = `query{${serviceName}(request:{page_size: ${store.pageSize}, page_index:${pageIndex},filter: {${store.filterStoreId? 'store_id:"'+store.filterStoreId+'",' : ''}${store.filterLocationId? 'location_id:'+store.filterLocationId+',' : ''}${store.filterInvoiceNumber? 'nomor_invoice:"'+store.filterInvoiceNumber+'",' : ''}${store.filterYear? 'invoice_year:'+store.filterYear+',' : ''}${store.filterMonth? 'invoice_month:'+store.filterMonth+',' : ''}}}){ status,error,count,page_count,list_data{id,nomor_invoice,receipt{id,nomor_kwitansi}} } }`
            let response = await api.graphqlInvoice(query, authToken)
            if (response.kind === "ok") {
                if(pageIndex == 0) store.setProp("invoices", response.invoices)
                else {
                    store.setProp("invoices", [...store.invoices, ...response.invoices])
                }
                store.setProp("pageCount", response.pageCount)
            } else {
                console.tron.error(`Error fetching: ${JSON.stringify(response)}`, [])
            }

        }
    },
    async reloadInvoices(authToken: string) {
        this.setInvoices([])
        this.setPageSize(6)
        this.setPageIndex(0)
        this.setPageCount(0)
        await this.fetchInvoices(authToken, 0)
    }
  }))
  .actions((store) => ({
    async submitReceiptForm(authToken: string, reloadReceipts: () => void) {
        const serviceName = 'upsertTbKwitansi'
        const query = `mutation{${serviceName}(request:{id:"${store.receiptFormId}",invoice_ids:[${store.receipts.map((invoiceId) => '"'+invoiceId+'"')}],${store.receiptFormYear? 'receipt_year:'+store.receiptFormYear+',' : ''}${store.receiptFormMonth? 'receipt_month:'+store.receiptFormMonth+',' : ''}}){ status,error,detail_data{id,nomor_kwitansi}}}`
        let response = await api.graphqlUpsertReceipt(query, authToken)
        if (response.kind === "ok") {
          store.setProp("receiptFormSubmitInprogress", false)
          store.setProp("receiptFormSubmitSuccessMessage", "submit invoice success")
          store.setProp("receiptFormSubmitErrorMessage", "")
          // reload receipt
          await reloadReceipts(authToken)
          // reset form
          store.toggleReceiptReset()
          // reload invoice
          await store.reloadInvoices(authToken)
          if(store.receiptFormIsCreateNew){
            await store.fetchReceiptFormReceipts(authToken)
          }
          store.setProp("receiptFormModalVisible", false)
        } else {
          store.setProp("receiptFormSubmitInprogress", false)
          store.setProp("receiptFormSubmitSuccessMessage", "")
          store.setProp("receiptFormSubmitErrorMessage", "submit invoice note failed")
        }
      },
    async fetchFilterLocations(authToken: string) {
        const serviceName = 'getAllTbLocation'
        const query = `query{${serviceName}(request:{page_size: 100, page_index:0, filter: {}}){ status,error,count,page_count,list_data{id,location_code,location_name}}}`
        let response = await api.graphqlLocation(query, authToken)
        if (response.kind === "ok") {
            store.setProp("filterLocations", response.locations)
        } else {
            console.tron.error(`Error fetching locations: ${JSON.stringify(response)}`, [])
        }
    },
    async fetchFilterStores(authToken: string) {
        const serviceName = 'getAllTbStore'
        const query = `query{${serviceName}(request:{page_size: 100, page_index:0, filter: { ${store.filterLocationId? 'store_location:'+store.filterLocationId+'' : ''}}}){ status,error,count,page_count,list_data{id,store_name,store_location,location{id,location_name}}}}`
        let response = await api.graphqlStore(query, authToken)
        if (response.kind === "ok") {
          store.setProp("filterStores", response.stores)
        } else {
          console.tron.error(`Error fetching locations: ${JSON.stringify(response)}`, [])
        }
    },
    setFilterModalVisible(value?: boolean) {
        store.filterModalVisible = value
    },
    setFilterLocationId(value?: string) {
        store.filterLocationId = value
    },
    setFilterStoreId(value?: string) {
        store.filterStoreId = value
      },
    setFilterYear(value?: string) {
        store.filterYear = value
    },
    setFilterMonth(value?: string) {
        store.filterMonth = value
    },
    setFilterInvoiceNumber(value?: string) {
        store.filterInvoiceNumber = value
    },
    addReceipt(invoice: Invoice) {
        store.receipts.push(invoice.id)
    },
    removeReceipt(invoice: Invoice) {
        store.receipts.remove(invoice.id)
    },
    
  })) // eslint-disable-line @typescript-eslint/no-unused-vars
  .views((store) => ({
    get invoicesForList() {
        return store.invoices
    },
    get isInvoicesNotEmpty() {
        return store.invoices.length > 0
    },
    get filterLocationComboboxOptions() {
        return store.filterLocations.map((location) => ({
            label: location.location_name,
            value: location.id
        }))
      },
    get filterStoreComboboxOptions() {
        return store.filterStores.map((item) => ({
            label: item.store_name,
            value: item.id
        }))
    },
    get receiptFormReceiptsComboboxOptions() {
        return store.receiptFormReceipts.map((receipt) => ({
            label: receipt.nomor_kwitansi,
            value: receipt.id
        }))
      },
    hasReceipt(invoice: Invoice) {
        return store.receipts.includes(invoice.id)
    },
    get isReceiptsNotEmpty() {
        return store.receipts.length > 0
      }
  })) // eslint-disable-line @typescript-eslint/no-unused-vars
  .actions((store) => ({
    toggleReceipt(invoice: Invoice) {
        if (store.hasReceipt(invoice)) {
            store.removeReceipt(invoice)
        } else {
            store.addReceipt(invoice)
        }
    }
  }))
  .actions((store) => ({
    setReceiptFormId(value?: string) {
        store.receiptFormId = value
      },
    setReceiptFormSubmitInprogress(value?: boolean) {
        store.receiptFormSubmitInprogress = value
      },
    setReceiptFormSubmitMessage(value?: string) {
        store.receiptFormSubmitMessage = value
    },
    setReceiptFormSubmitErrorMessage(value?: string) {
        store.receiptFormSubmitErrorMessage = value
    },
    setReceiptFormSubmitSuccessMessage(value?: string) {
        store.receiptFormSubmitSuccessMessage = value
    },
    setReceiptFormYear(value?: string) {
        store.receiptFormYear = value
    },
    setReceiptFormMonth(value?: string) {
        store.receiptFormMonth = value
    },
    
    setReceiptFormIsCreateNew(value?: boolean) {
        store.receiptFormIsCreateNew = value
    },
    setReceiptFormModalVisible(value?: boolean) {
        store.receiptFormModalVisible = value
      },
  })) // eslint-disable-line @typescript-eslint/no-unused-vars

export interface InvoiceStore extends Instance<typeof InvoiceStoreModel> {}
export interface InvoiceStoreSnapshotOut extends SnapshotOut<typeof InvoiceStoreModel> {}
export interface InvoiceStoreSnapshotIn extends SnapshotIn<typeof InvoiceStoreModel> {}
export const createInvoiceStoreDefaultModel = () => types.optional(InvoiceStoreModel, {})
