export interface UpsertFormModalProps {
    locationId?: string,
    storeId?: string,
    hideButtonOpen?: boolean,
    labelButtonOpen?: string,
}
export const UpsertFormModal: FC<UpsertFormModalProps> = observer(function UpsertFormModal(_props) {
    const [modalVisible, setModalVisible] = React.useState(false);
    const [selected, setSelected] = useState(undefined);
    const { hideButtonOpen, labelButtonOpen } = _props;
    const {
        receiptStore: {
            setUpsertFormModalVisible,
            upsertFormModalVisible,
            setUpsertFormId,
            upsertFormId,
            upserteFormSubmitInprogress,
            upsertFormSubmitErrorMessage,
            upsertFormSubmitSuccessMessage,
            upsertFormReceiptNumber,
            setUpsertFormReceiptNumber
        },
        authenticationStore: { authToken },
      } = useStores()

    // if (!upsertFormModalVisible)
    //     return null

    return (
        <Modal 
            visible={upsertFormModalVisible}
            onRequestClose={() => {setUpsertFormModalVisible(false)}}
            onPressClose={() => {setUpsertFormModalVisible(false)}}
        >
            <Text style={styles.modalText}>{`${upsertFormId? 'Update':'Create'}`} Receipt Form</Text>
            {upserteFormSubmitInprogress && <Text style={{ color: colors.palette.primary600 }}>Inprogress...</Text>}
            {!!upsertFormSubmitErrorMessage && <Text style={{ color: colors.error }}>{upsertFormSubmitErrorMessage}</Text>}
            {!!upsertFormSubmitSuccessMessage && <Text style={{ color: "green" }}>{upsertFormSubmitSuccessMessage}</Text>}
            {!!upsertFormReceiptNumber && <TextField
                editable={false}
                value={upsertFormReceiptNumber}
                onChangeText={setUpsertFormReceiptNumber}
                containerStyle={$textField}
                autoCapitalize="none"
                autoComplete="name"
                autoCorrect={false}
                keyboardType="default"
                labelTx="upsertFormModal.deliveryNoteNumberFieldLabel"
                placeholderTx="upsertFormModal.deliveryNoteNumberFieldPlaceholder"
            />}
            <View>
                {!!upsertFormReceiptNumber && <Pressable
                style={[styles.button, styles.buttonClose, {marginTop: 10}]}
                onPress={() => openLinkInBrowser(`${Config.API_URL}udgisi/print-receipt?number=${encodeURIComponent(upsertFormReceiptNumber)}`)}>
                <Text style={styles.textStyle}>Print</Text>
                </Pressable>}
            </View>
        </Modal> 
    );
});

