import {
  DarkTheme,
  DefaultTheme,
  NavigationContainer,
  NavigatorScreenParams, // @demo remove-current-line
} from "@react-navigation/native"
import { createNativeStackNavigator, NativeStackScreenProps } from "@react-navigation/native-stack"
import { observer } from "mobx-react-lite"
import React from "react"
import { useColorScheme } from "react-native"
import Config from "../config"
import { useStores } from "../models" // @demo remove-current-line
import { DemoNavigator, DemoTabParamList } from "./DemoNavigator" // @demo remove-current-line
import { navigationRef, useBackButtonHandler } from "./navigationUtilities"
import { colors } from "app/theme"
import {
  WelcomeScreen,
  BankingLoginScreen
} from "app/screens"

export type BankingNavigatorParamList = {
  Demo: undefined,
  Welcome: undefined
  Login: undefined // @demo remove-current-line
}

const exitRoutes = Config.exitRoutes
export type BankingStackScreenProps<T extends keyof BankingNavigatorParamList> = NativeStackScreenProps<
BankingNavigatorParamList,
  T
>

const Stack = createNativeStackNavigator<BankingNavigatorParamList>()
const AppStack = observer(function AppStack() {
  const {
    authenticationStore: { isAuthenticated },
  } = useStores()

  return (
    <Stack.Navigator
      screenOptions={{ headerShown: false, navigationBarColor: colors.background }}
      initialRouteName={isAuthenticated ? "Welcome" : "Login"} // @demo remove-current-line
    >
      {
        isAuthenticated ? (
          <>
            <Stack.Screen name="Welcome" component={WelcomeScreen} />
            <Stack.Screen name="Demo" component={DemoNavigator} />
          </>
        ) : (
          <>
            <Stack.Screen name="Login" component={BankingLoginScreen} />
          </>
        )
      }
    </Stack.Navigator>
  )
})
export interface BankingNavigationProps
  extends Partial<React.ComponentProps<typeof NavigationContainer>> {}

export const BankingNavigator = observer(function BankingNavigator(props: BankingNavigationProps) {
  const colorScheme = useColorScheme()

  useBackButtonHandler((routeName) => exitRoutes.includes(routeName))

  return (
    <NavigationContainer
      ref={navigationRef}
      theme={colorScheme === "dark" ? DarkTheme : DefaultTheme}
      {...props}
    >
      <AppStack />
    </NavigationContainer>
  )
})
