import { Instance, SnapshotIn, SnapshotOut, types } from "mobx-state-tree"
import { withSetPropAction } from "./helpers/withSetPropAction"
import { api } from "../services/api"
import { Delnote, DelnoteModel } from "./Delnote"
import { Store, StoreModel } from "./Store"
import { InvoiceModel } from "./Invoice"
import { LocationModel } from "./Location"

/**
 * Model description here for TypeScript hints.
 */
export const DelnoteStoreModel = types
  .model("DelnoteStore")
  .props({
    deliverynotes: types.array(DelnoteModel),
    invoices: types.optional(types.array(types.string), []),
    // filtering
    filterStores: types.array(StoreModel),
    filterLocations: types.array(LocationModel),
    filterLocationId: "",
    filterStoreId: "",
    filterDeliveryNoteNumber: "",
    filterDeliveryYear: "",
    filterDeliveryMonth: "",
    filterDeliveryDate: "",
    filterModalVisible: false,
    // createForm
    createFormStores: types.array(StoreModel),
    createFormLocations: types.array(LocationModel),
    createFormId: "",
    createFormLocationId: "",
    createFormStoreId: "",
    createFormDeliveryNoteNumber: "",
    createFormDeliveryYear: "",
    createFormDeliveryMonth: "",
    createFormDeliveryDate: "",
    createFormTotalCase: 0,
    createFormSubmitInprogress: false,
    createFormSubmitMessage: "",
    createFormSubmitErrorMessage: "",
    createFormSubmitSuccessMessage: "",
    createFormModalVisible: false,

    // invoice form
    invoiceFormId: "",
    invoiceFormInvoices: types.optional(types.array(InvoiceModel), []),
    invoiceFormStores: types.array(StoreModel),
    invoiceFormLocations: types.array(LocationModel),
    invoiceFormStoreId: "",
    invoiceFormLocationId: "",
    invoiceFormYear: "",
    invoiceFormMonth: "",
    invoiceFormIsCreateNew: true,
    invoiceFormModalVisible: false,
    invoiceFormSubmitInprogress: false,
    invoiceFormSubmitMessage: "",
    invoiceFormSubmitErrorMessage: "",
    invoiceFormSubmitSuccessMessage: "",

    // pagination
    pageIndex: 0,
    pageSize: 6,
    pageCount: 0,
    column: "id,nomor_surat,store_id,version,quantity,price,created_dt,updated_dt,version,created_by,updated_dt,delivery_year,delivery_month,delivery_date,total_case,invoice{id,nomor_invoice},kwitansi{id,nomor_kwitansi},store{id,store_name,location{id,location_name}}"
  })
  .actions(withSetPropAction)
  .actions((store) => ({
    async fetchInvoiceFormInvoices(authToken: string) {
      const serviceName = 'getAllTbInvoice'
      // console.log('before query')
      const query = `query{${serviceName}(request:{page_size: 10000, page_index:0, filter: { ${store.invoiceFormLocationId? 'location_id:'+store.invoiceFormLocationId+',' : ''}${store.invoiceFormStoreId? 'store_id:"'+store.invoiceFormStoreId+'",':''}${store.invoiceFormYear? 'invoice_year:'+store.invoiceFormYear+',':''}${store.invoiceFormMonth? 'invoice_month:'+store.invoiceFormMonth:''} }}){ status,error,count,page_count,list_data{id,nomor_invoice}}}`
      // console.log('after query ', query)
      let response = await api.graphqlInvoice(query, authToken)
      // console.log('query done', response)
      if (response.kind === "ok") {
        // console.log('create form stores==>', response.stores)
        store.setProp("invoiceFormInvoices", response.invoices)
      } else {
        // console.log('create form stores gagal==>', response.stores)
        console.tron.error(`Error fetching locations: ${JSON.stringify(response)}`, [])
      }
    },
    async fetchDelnotes(authToken: string, pageIndex: number, logout: () => void) {
      store.setProp("pageIndex", pageIndex)
      if(pageIndex > 0 && store.pageCount <= pageIndex){

      } else {
        if(pageIndex == 0) store.setProp("pageCount", 0)
        store.setProp("pageCount", 0)
        const serviceName = 'getAllTbSuratjalan'
        let query = `query{${serviceName}(request:{page_size: ${store.pageSize}, page_index:${pageIndex},filter: {${store.filterDeliveryDate? 'delivery_date:'+store.filterDeliveryDate+',' : ''}${store.filterDeliveryMonth? 'delivery_month:'+store.filterDeliveryMonth+',' : ''}${store.filterDeliveryYear? 'delivery_year:'+store.filterDeliveryYear+',' : ''}${store.filterStoreId? 'store_id:"'+store.filterStoreId+'",' : ''}${store.filterLocationId? 'location_id:'+store.filterLocationId+',' : ''}${store.filterDeliveryNoteNumber? 'nomor_surat:"'+store.filterDeliveryNoteNumber+'",' : ''}}}){ status,error,count,page_count,list_data{${store.column}} } }`
        // if(!store.filterStoreId && store.filterLocationId){
        //   query = `query{${serviceName}(request:{page_size: ${store.pageSize}, page_index:${pageIndex},filter: {${store.filterLocationId? 'location_id:'+store.filterLocationId+',' : ''}${store.filterDeliveryNoteNumber? 'nomor_surat:"'+store.filterDeliveryNoteNumber+'",' : ''}}}){ status,error,count,page_count,list_data{${store.column}} } }`
        // }
        let response = await api.graphqlDelnote(query, authToken)
        if (response.kind === "ok") {
          if(pageIndex == 0) store.setProp("deliverynotes", response.delnotes)
          else {
            store.setProp("deliverynotes", [...store.deliverynotes, ...response.delnotes])
          }
          store.setProp("pageCount", response.pageCount)
        } else if(response.kind === "unauthorized"){
          // unauthorized
          store.setProp("filterModalVisible", false)
          store.setProp("invoiceFormModalVisible", false)
          store.setProp("createFormModalVisible", false)
          logout()
        } else {
          console.tron.error(`Error fetching episodes: ${JSON.stringify(response)}`, [])
        }
      }
    },
    setPageIndex(value?: number) {
      store.pageIndex = value
    },
    setPageSize(value?: number) {
      store.pageSize = value
    },
    setPageCount(value?: number) {
      store.pageCount = value
    },
    
    // set deliverynotes
    setDeliverynotes(value?: any) {
      store.deliverynotes = value
    },
    toggleInvoiceReset() {
      store.invoices.clear()
    },
    async reloadDelnotes(authToken: string, logout: () => void) {
      this.setDeliverynotes([])
      this.setPageSize(6)
      this.setPageIndex(0)
      this.setPageCount(0)
      await this.fetchDelnotes(authToken, 0, logout)
    }
  }))
  .actions((store) => ({
    async submitCreateForm(authToken: string) {
      console.log('submit form')
      const serviceName = 'upsertTbSuratjalan'
      const query = `mutation{${serviceName}(request:{id:"${store.createFormId}",delivery_year: ${store.createFormDeliveryYear}, delivery_month: ${store.createFormDeliveryMonth}, delivery_date: ${store.createFormDeliveryDate}, quantity: ${store.createFormTotalCase}, store_id: "${store.createFormStoreId}"}){ status,error,detail_data{id,nomor_surat,store_id,version,quantity,price,created_dt,updated_dt,version,created_by,updated_dt,delivery_year,delivery_month,delivery_date,total_case,store{id,store_name,location{id,location_name}}}}}`
      let response = await api.graphqlCreateDelnote(query, authToken)
      if (response.kind === "ok") {
        store.setProp("createFormSubmitInprogress", false)
        store.setProp("createFormSubmitSuccessMessage", "Submit delivery note success")
        store.setProp("createFormSubmitErrorMessage", "")
        if(store.createFormId){
          store.setProp("createFormDeliveryNoteNumber", response.delnotes.nomor_surat)
        }
      } else {
        store.setProp("createFormSubmitInprogress", false)
        store.setProp("createFormSubmitSuccessMessage", "")
        store.setProp("createFormSubmitErrorMessage", "Submit delivery note failed")
      }
    },
    async fetchFilterLocations(authToken: string) {
      const serviceName = 'getAllTbLocation'
      const query = `query{${serviceName}(request:{page_size: 100, page_index:0, filter: {}}){ status,error,count,page_count,list_data{id,location_code,location_name}}}`
      let response = await api.graphqlLocation(query, authToken)
      if (response.kind === "ok") {
          store.setProp("filterLocations", response.locations)
      } else {
          console.tron.error(`Error fetching locations: ${JSON.stringify(response)}`, [])
      }
    },
    async fetchFilterStores(authToken: string) {
      const serviceName = 'getAllTbStore'
      const query = `query{${serviceName}(request:{page_size: 100, page_index:0, filter: { ${store.filterLocationId? 'store_location:'+store.filterLocationId+'' : ''}}}){ status,error,count,page_count,list_data{id,store_name,store_location,location{id,location_name}}}}`
      let response = await api.graphqlStore(query, authToken)
      if (response.kind === "ok") {
        store.setProp("filterStores", response.stores)
      } else {
        console.tron.error(`Error fetching locations: ${JSON.stringify(response)}`, [])
      }
    },
    async fetchCreateFormLocations(authToken: string) {
      const serviceName = 'getAllTbLocation'
      const query = `query{${serviceName}(request:{page_size: 100, page_index:0, filter: {}}){ status,error,count,page_count,list_data{id,location_code,location_name}}}`
      let response = await api.graphqlLocation(query, authToken)
      if (response.kind === "ok") {
          store.setProp("createFormLocations", response.locations)
      } else {
          console.tron.error(`Error fetching locations: ${JSON.stringify(response)}`, [])
      }
    },
    async fetchCreateFormStores(authToken: string) {
      const serviceName = 'getAllTbStore'
      // console.log('before query')
      const query = `query{${serviceName}(request:{page_size: 100, page_index:0, filter: { ${store.createFormLocationId? 'store_location:'+store.createFormLocationId+'' : ''}}}){ status,error,count,page_count,list_data{id,store_name,store_location,location{id,location_name}}}}`
      // console.log('after query ', query)
      let response = await api.graphqlStore(query, authToken)
      // console.log('query done', response)
      if (response.kind === "ok") {
        // console.log('create form stores==>', response.stores)
        store.setProp("createFormStores", response.stores)
      } else {
        // console.log('create form stores gagal==>', response.stores)
        console.tron.error(`Error fetching locations: ${JSON.stringify(response)}`, [])
      }
    },
    
    setFilterDeliveryNoteNumber(value?: string) {
      store.filterDeliveryNoteNumber = value
    },
    setCreateFormStoreId(value?: string) {
      store.createFormStoreId = value
    },
    setCreateFormLocationId(value?: string) {
      store.createFormLocationId = value
    },
    setFilterDeliveryYear(value?: string) {
      store.filterDeliveryYear = value
    },
    setFilterDeliveryMonth(value?: string) {
      store.filterDeliveryMonth = value
    },
    setFilterDeliveryDate(value?: string) {
      store.filterDeliveryDate = value
    },
    setCreateFormDeliveryDate(value?: string) {
      store.createFormDeliveryDate = value
    },
    
    setFilterLocationId(value?: string) {
      store.filterLocationId = value
    },
    setFilterStoreId(value?: string) {
      store.filterStoreId = value
    },
    setCreateFormDeliveryYear(value?: string) {
      store.createFormDeliveryYear = value
    },
    setCreateFormDeliveryMonth(value?: string) {
      store.createFormDeliveryMonth = value
    },
    setCreateFormTotalCase(value?: number) {
      store.createFormTotalCase = value
    },
    setCreateFormSubmitInprogress(value?: boolean) {
      store.createFormSubmitInprogress = value
    },
    setCreateFormSubmitMessage(value?: string) {
      store.createFormSubmitMessage = value
    },
    setCreateFormSubmitErrorMessage(value?: string) {
      store.createFormSubmitErrorMessage = value
    },
    setCreateFormSubmitSuccessMessage(value?: string) {
      store.createFormSubmitSuccessMessage = value
    },
    setCreateFormModalVisible(value?: boolean) {
      store.createFormModalVisible = value
    },
    setFilterModalVisible(value?: boolean) {
      store.filterModalVisible = value
    },
    setCreateFormId(value?: string) {
      store.createFormId = value
    },
    setcreateFormDeliveryNoteNumber(value?: string) {
      store.createFormDeliveryNoteNumber = value
    },
    addInvoice(delnote: Delnote) {
      store.invoices.push(delnote.id)
    },
    removeInvoice(delnote: Delnote) {
      store.invoices.remove(delnote.id)
    },

  })) // eslint-disable-line @typescript-eslint/no-unused-vars
  .views((store) => ({
    get deliveryNotesForList() {
      return store.deliverynotes
    },
    get filterStoreComboboxOptions() {
      return store.filterStores.map((item) => ({
          label: item.store_name,
          value: item.id
      }))
    },
    get filterLocationComboboxOptions() {
      return store.filterLocations.map((location) => ({
          label: location.location_name,
          value: location.id
      }))
    },
    get createFormStoreComboboxOptions() {
      return store.createFormStores.map((item) => ({
          label: item.store_name,
          value: item.id
      }))
    },
    get invoiceFormStoreComboboxOptions() {
      return store.invoiceFormStores.map((item) => ({
          label: item.store_name,
          value: item.id
      }))
    },
    get createFormLocationComboboxOptions() {
      return store.createFormLocations.map((location) => ({
          label: location.location_name,
          value: location.id
      }))
    },
    get invoiceFormLocationComboboxOptions() {
      return store.invoiceFormLocations.map((location) => ({
          label: location.location_name,
          value: location.id
      }))
    },
    get invoiceFormInvoicesComboboxOptions() {
      return store.invoiceFormInvoices.map((invoice) => ({
          label: invoice.nomor_invoice,
          value: invoice.id
      }))
    },
    hasInvoice(delnote: Delnote) {
      return store.invoices.includes(delnote.id)
    },
    get isInvoicesNotEmpty() {
      return store.invoices.length > 0
    }
  })) // eslint-disable-line @typescript-eslint/no-unused-vars
  .actions((store) => ({
    toggleInvoice(delnote: Delnote) {
      console.log('toggleInvoice', delnote)
      if (store.hasInvoice(delnote)) {
        console.log('removeInvoice', delnote.id)
        store.removeInvoice(delnote)
      } else {
        console.log('addInvoice', delnote.id)
        store.addInvoice(delnote)
      }
    },
  }))
  .actions((store) => ({
    async submitInvoiceForm(authToken: string, reloadInvoices: (authToken: string) => void, logout: () => void) {
      const serviceName = 'upsertTbInvoice'
      const query = `mutation{${serviceName}(request:{id:"${store.invoiceFormId}",suratjalan_ids:[${store.invoices.map((id) => '"'+id+'"')}],invoice_year: ${store.invoiceFormYear}, invoice_month: ${store.invoiceFormMonth}, store_id: "${store.invoiceFormStoreId}"}){ status,error,detail_data{id,nomor_invoice}}}`
      let response = await api.graphqlUpsertInvoice(query, authToken)
      if (response.kind === "ok") {
        store.setProp("invoiceFormSubmitInprogress", false)
        store.setProp("invoiceFormSubmitSuccessMessage", "submit invoice success")
        store.setProp("invoiceFormSubmitErrorMessage", "")
        // get invoice
        await reloadInvoices(authToken)
        // reset form
        store.toggleInvoiceReset()
        // refresh delnot
        await store.reloadDelnotes(authToken, logout)
        // reload data
        // store.setPageIndex(0)
        // store.setPageCount(0)
        // await store.fetchDelnotes(authToken, 0, logout)
        if(store.invoiceFormIsCreateNew){
          await store.fetchInvoiceFormInvoices(authToken)
        }
        store.setProp("invoiceFormModalVisible", false)
      } else {
        store.setProp("invoiceFormSubmitInprogress", false)
        store.setProp("invoiceFormSubmitSuccessMessage", "")
        store.setProp("invoiceFormSubmitErrorMessage", "submit invoice note failed")
      }
    },
    async fetchInvoiceFormLocations(authToken: string) {
      const serviceName = 'getAllTbLocation'
      const query = `query{${serviceName}(request:{page_size: 100, page_index:0, filter: {}}){ status,error,count,page_count,list_data{id,location_code,location_name}}}`
      let response = await api.graphqlLocation(query, authToken)
      if (response.kind === "ok") {
          store.setProp("invoiceFormLocations", response.locations)
      } else {
          console.tron.error(`Error fetching locations: ${JSON.stringify(response)}`, [])
      }
    },
    async fetchInvoiceFormStores(authToken: string) {
      const serviceName = 'getAllTbStore'
      // console.log('before query')
      const query = `query{${serviceName}(request:{page_size: 100, page_index:0, filter: { ${store.invoiceFormLocationId? 'store_location:'+store.invoiceFormLocationId+'' : ''}}}){ status,error,count,page_count,list_data{id,store_name,store_location,location{id,location_name}}}}`
      // console.log('after query ', query)
      let response = await api.graphqlStore(query, authToken)
      // console.log('query done', response)
      if (response.kind === "ok") {
        // console.log('create form stores==>', response.stores)
        store.setProp("invoiceFormStores", response.stores)
      } else {
        // console.log('create form stores gagal==>', response.stores)
        console.tron.error(`Error fetching locations: ${JSON.stringify(response)}`, [])
      }
    },
    
    setInvoiceFormId(value?: string) {
      store.invoiceFormId = value
    },
    setInvoiceFormStoreId(value?: string) {
      store.invoiceFormStoreId = value
    },
    setInvoiceFormLocationId(value?: string) {
      store.invoiceFormLocationId = value
    },
    setInvoiceFormYear(value?: string) {
      store.invoiceFormYear = value
    },
    setInvoiceFormMonth(value?: string) {
      store.invoiceFormMonth = value
    },
    setInvoiceFormModalVisible(value?: boolean) {
      store.invoiceFormModalVisible = value
    },
    setInvoiceFormSubmitInprogress(value?: boolean) {
      store.invoiceFormSubmitInprogress = value
    },
    setInvoiceFormSubmitMessage(value?: string) {
      store.invoiceFormSubmitMessage = value
    },
    setInvoiceFormSubmitErrorMessage(value?: string) {
      store.invoiceFormSubmitErrorMessage = value
    },
    setInvoiceFormSubmitSuccessMessage(value?: string) {
      store.invoiceFormSubmitSuccessMessage = value
    },
    setInvoiceFormIsCreateNew(value?: boolean) {
      store.invoiceFormIsCreateNew = value
    }
  })) // eslint-disable-line @typescript-eslint/no-unused-vars

export interface DelnoteStore extends Instance<typeof DelnoteStoreModel> {}
export interface DelnoteStoreSnapshotOut extends SnapshotOut<typeof DelnoteStoreModel> {}
export interface DelnoteStoreSnapshotIn extends SnapshotIn<typeof DelnoteStoreModel> {}
export const createDelnoteStoreDefaultModel = () => types.optional(DelnoteStoreModel, {})
