import * as React from "react"
import { StyleProp, TouchableWithoutFeedback, TextInput, TextStyle, ViewStyle, Platform, View, ScrollView, StyleSheet, Modal as RNModal, Alert, Pressable, TouchableOpacity, ModalBaseProps } from "react-native"
import { observer } from "mobx-react-lite"
import { colors, typography } from "app/theme"
import { Text } from "app/components/Text"

export interface ModalProps extends ModalBaseProps {
  text?: string,
  visible?: boolean,
  onClose?: () => void,
  onPressClose?: () => void,
  children?: React.ReactNode
}

export function Modal(props: ModalProps) {
  const { visible, onClose, onPressClose, children, ...rest } = props

  return (
      <View>
          <RNModal 
              animationType="fade"
              transparent={true}
              visible={visible}
              onRequestClose={onClose}
              {...rest}
              >
                  {/* <TouchableWithoutFeedback onPress={() => onPressClose()}> */}
                      <View style={styles.modalOverlay} />
                  {/* </TouchableWithoutFeedback> */}

                  
                  <View style={styles.modalContent}>
                      
                                      <Pressable 
                                  style={[styles.centeredView, styles.overlay, { backgroundColor: 'rgba(0,0,0,0)' }]} 
                                  // onPress={}
                                  // activeOpacity={1} 
                                  onPress={onPressClose}
                              >
                                      <ScrollView 
                                          directionalLockEnabled={true} 
                                          contentContainerStyle={{ flexGrow: 1, justifyContent: 'center'}}
                                      >
                                          <TouchableWithoutFeedback>
                                              <View style={[{ backgroundColor: 'yellow', padding: 10 }]}>
                                                  {children}
                                              </View>
                                          </TouchableWithoutFeedback>
                                      </ScrollView>
                              </Pressable>   
                  </View>

                  
              </RNModal> 
          </View>
  );
}

