import { Instance, SnapshotOut, types } from "mobx-state-tree"
import { decryptAccessToken } from '../utils/security'
import { api } from "../services/api"
import { withSetPropAction } from "./helpers/withSetPropAction"

export const AuthenticationStoreModel = types
  .model("AuthenticationStore")
  .props({
    authToken: types.maybe(types.string),
    authEmail: "",
    authPassword: "",
    userFullName: "",
    userEmail: "",
    userRole: "",
    userPrivileges: types.optional(types.array(types.string), []),
    userLoginInprogress: false,
    userLogoutInprogress: false,
  })
  .actions(withSetPropAction)
  .actions((store) => ({
    setUserLoginInprogress(value: boolean) {
      store.userLoginInprogress = value
    },
    setUserLogoutInprogress(value: boolean) {
      store.userLogoutInprogress = value
    }
  }))
  .views((store) => ({
    get isAuthenticated() {
      return !!store.authToken
    },
    get validationError() {
      if (store.authEmail.length === 0) return "can't be blank"
      if (store.authEmail.length < 6) return "must be at least 6 characters"
      if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(store.authEmail))
        return "must be a valid email address"
      return ""
    },
  }))
  .actions((store) => ({
    async loginDoLogin(){
      store.setUserLoginInprogress(true)
      const query = `query{login(username:"${store.authEmail}", password:"${store.authPassword}"){ access_token status error user{full_name, username, email, user_id} user_privileges role user_merchants{id, merchant_name, merchant_code, is_default, plan} } }`
      const response = await api.graphql(query)
      store.setUserLoginInprogress(false)
      return response
    },
    setAuthToken(value?: string) {
      store.authToken = value
    },
    setAuthEmail(value: string) {
      store.authEmail = value.replace(/ /g, "")
    },
    setUserFullName(value?: string) {
      store.userFullName = value
    },
    setUserEmail(value?: string) {
      store.userEmail = value
    },
    setUserRole(value?: string) {
      store.userRole = value
    },
    setAuthPassword(value: string) {
      store.authPassword = value.replace(/ /g, "")
    },
    //set user privileges with type types.optional(types.array(types.string), []),
    setUserPrivileges(value?: any) {
      console.log('setUserPrivileges=', value)
      store.userPrivileges = value
    },
    logout() {
      store.authToken = undefined
      store.authEmail = ""
    },
  }))
  .views((store) => ({
    hasPriv(value: string) {
      return store.userPrivileges.includes(value)
    }
  }))

export interface AuthenticationStore extends Instance<typeof AuthenticationStoreModel> {}
export interface AuthenticationStoreSnapshot extends SnapshotOut<typeof AuthenticationStoreModel> {}

