import { Instance, SnapshotIn, SnapshotOut, types } from "mobx-state-tree"
import { withSetPropAction } from "./helpers/withSetPropAction"
import { LocationModel } from "./Location"
import { api } from "../services/api"

/**
 * Model description here for TypeScript hints.
 */
export const LocationStoreModel = types
  .model("LocationStore")
  .props({
    locations: types.array(LocationModel),
    locationName: "",
    pageIndex: 0,
    pageSize: 6,
    pageCount: 0
  })
  .actions(withSetPropAction)
  .actions((store) => ({
    async fetchLocations(authToken: string) {
        const serviceName = 'getAllTbLocation'
        const query = `query{${serviceName}(request:{page_size: ${store.pageSize}, page_index:${store.pageIndex}, filter: { ${store.locationName? 'location_name:"'+store.locationName+'"' : ''}}}){ status,error,count,page_count,list_data{id,location_code,location_name}}}`
        let response = await api.graphqlLocation(query, authToken)
        if (response.kind === "ok") {
            store.setProp("locations", response.locations)
        } else {
            console.tron.error(`Error fetching locations: ${JSON.stringify(response)}`, [])
        }
    }
  }))
  .views((store) => ({
      get locationComboboxOptions() {
          return store.locations.map((location) => ({
              label: location.location_name,
              value: location.id
          }))
      }
      // get locationsForList() {
      //     return store.favoritesOnly ? store.favorites : store.locations
      // }
  }))

export interface LocationStore extends Instance<typeof LocationStoreModel> {}
export interface LocationStoreSnapshotOut extends SnapshotOut<typeof LocationStoreModel> {}
export interface LocationStoreSnapshotIn extends SnapshotIn<typeof LocationStoreModel> {}
export const createLocationStoreDefaultModel = () => types.optional(LocationStoreModel, {})
