import { BottomTabScreenProps, createBottomTabNavigator } from "@react-navigation/bottom-tabs"
import { CompositeScreenProps } from "@react-navigation/native"
import React from "react"
import { TextStyle, ViewStyle, StyleSheet, View } from "react-native"
import { useSafeAreaInsets } from "react-native-safe-area-context"
import { Icon } from "../components"
import { translate } from "../i18n"
import { DemoCommunityScreen, DemoShowroomScreen, DemoDebugScreen, DelnoteScreen, InvoiceScreen, ReceiptScreen, ProfileScreen } from "../screens"
import { DemoPodcastListScreen } from "../screens/DemoPodcastListScreen"
import { colors, spacing, typography } from "../theme"
import { AppStackParamList, AppStackScreenProps } from "./AppNavigator"
import { TouchableOpacity } from "react-native-gesture-handler"


export type DemoTabParamList = {
  DemoCommunity: undefined
  DemoShowroom: { queryIndex?: string; itemIndex?: string }
  DemoDebug: undefined
  DemoPodcastList: undefined

  InvoiceScreen: undefined
  InvoiceScreen2: undefined
  DelnoteScreen: undefined
  ReceiptScreen: undefined
  ProfileScreen: undefined
}

/**
 * Helper for automatically generating navigation prop types for each route.
 *
 * More info: https://reactnavigation.org/docs/typescript/#organizing-types
 */
export type DemoTabScreenProps<T extends keyof DemoTabParamList> = CompositeScreenProps<
  BottomTabScreenProps<DemoTabParamList, T>,
  AppStackScreenProps<keyof AppStackParamList>
>

const Tab = createBottomTabNavigator<DemoTabParamList>()

export function DemoNavigator() {
  const { bottom } = useSafeAreaInsets()

  return (
    <Tab.Navigator
      screenOptions={{
        headerShown: false,
        tabBarHideOnKeyboard: true,
        tabBarStyle: [$tabBar, { height: bottom + 90 }, styles.shadow],
        tabBarActiveTintColor: colors.text,
        tabBarInactiveTintColor: colors.text,
        tabBarLabelStyle: $tabBarLabel,
        tabBarItemStyle: $tabBarItem,
      }}
    >
      <Tab.Screen
        name="DelnoteScreen"
        component={DelnoteScreen}
        options={{
          tabBarLabel: translate("demoNavigator.delnoteTab"),
          tabBarIcon: ({ focused }) => (
            <Icon icon="slack" color={focused && colors.tint} size={30} />
          ),
        }}
      />

      <Tab.Screen
        name="InvoiceScreen"
        component={InvoiceScreen}
        options={{
          tabBarLabel: translate("demoNavigator.invoiceTab"),
          tabBarIcon: ({ focused }) => (
            <Icon icon="podcast" color={focused && colors.tint} size={30} />
          ),
        }}
      />

      {/* <Tab.Screen
        name="InvoiceScreen2"
        component={DemoShowroomScreen}
        options={{
          // tabBarLabel: translate("demoNavigator.invoiceTab"),
          tabBarLabel: '',
          tabBarIcon: ({ focused }) => (
            <View style={{
              top: -30,
              backgroundColor: colors.palette.angry100,
              borderColor: colors.palette.angry500,
              borderWidth: 2,
              borderRadius: 40,
              width: 70,
              height: 70,
              justifyContent: "center",
              alignItems: "center",
            }}>
              <Icon icon="podcast" color={focused && colors.tint} size={30} />
            </View>
          ),
        }}
      /> */}

      <Tab.Screen
        name="ReceiptScreen"
        component={ReceiptScreen}
        options={{
          tabBarAccessibilityLabel: translate("demoNavigator.receiptTab"),
          tabBarLabel: translate("demoNavigator.receiptTab"),
          tabBarIcon: ({ focused }) => (
            <Icon icon="clap" color={focused && colors.tint} size={30} />
          ),
        }}
      />

      <Tab.Screen
        name="ProfileScreen"
        component={ProfileScreen}
        options={{
          tabBarLabel: translate("demoNavigator.profileTab"),
          tabBarIcon: ({ focused }) => (
            <Icon icon="community" color={focused && colors.tint} size={30} />
          ),
        }}
      />
    </Tab.Navigator>
  )
}

const $tabBar: ViewStyle = {
  position: "absolute",
  bottom: 0,
  left: 5,
  right: 5,
  elevation: 0,
  borderRadius: 15,
  backgroundColor: colors.palette.angry100,
  borderTopColor: colors.transparent,
}

const $tabBarItem: ViewStyle = {
  paddingTop: spacing.md,
}

const $tabBarLabel: TextStyle = {
  fontSize: 12,
  fontFamily: typography.primary.medium,
  lineHeight: 16,
  flex: 1,
}

const styles = StyleSheet.create({
  shadow: {
    shadowColor: colors.palette.neutral800,
    shadowOffset: {
      width: 0,
      height: 10,
    },
    shadowOpacity: 0.25,
    shadowRadius: 3.5,
    elevation: 5,
  },
})

