  async graphqlCreateDelnote(query: string, accessToken?: string): Promise<{ kind: "ok"; delnotes: DelnoteSnapshotIn } | GeneralApiProblem> {
    const body = { query: query }
    this.apisauce2.setHeader('hmac', generateHmac(JSON.stringify(body)))
    if(accessToken) this.apisauce2.setHeader('AccessToken', accessToken)
    const response: ApiResponse<ApiGraphqlCreateDelnoteResponse> = await this.apisauce2.post("/" + Config.graphqlPath, body)
    // if (!response.ok) {
      const problem = getGeneralApiProblem(response)
      if (problem) return problem
    // }
    try{
      const rawData = response.data
      const delnotes: DelnoteSnapshotIn = rawData.data.upsertTbSuratjalan.detail_data
      return { kind: "ok", delnotes }
    }catch(e){
      if (__DEV__) {
        console.tron.error(`Bad data: ${e.message}\n${response.data}`, e.stack)
      }
      return { kind: "bad-data" }
    }
  }
