import * as React from "react"
import { StyleProp, TextStyle, View, ViewStyle, Pressable, PressableProps } from "react-native"
import { observer } from "mobx-react-lite"
import { colors, typography } from "app/theme"
import { Text } from 'react-native-paper';
// import { Text } from "app/components/Text"
import { Icon, IconTypes } from "app/components/Icon"

export interface IconRoundedProps extends PressableProps {
  /**
   * An optional style override useful for padding & margin.
   */
  style?: StyleProp<ViewStyle>,
  icon: IconTypes,
  backgroundColor: string,
  text?: string,
  roundedSize: number,
  iconSize: number,
}

/**
 * Describe your component here
 */
export const IconRounded = observer(function IconRounded(props: IconRoundedProps) {
  const { style, icon, backgroundColor, text, iconSize, roundedSize, ...rest } = props
  const $styles = [$container, style]

  return (
    <Pressable style={{flex: 1, justifyContent:'center', alignItems: 'center' }} {...rest}>
      <View style={{ backgroundColor: backgroundColor, padding:roundedSize, borderRadius: 10, justifyContent:'center', alignItems: 'center' }} >
        <View style={{ backgroundColor: 'white', width: iconSize, height: iconSize, borderRadius: 10, justifyContent:'center', alignItems: 'center' }} >
          <Icon icon={icon} color={backgroundColor} size={iconSize/2} />
        </View>
      </View>
      {!!text && <Text variant="titleMedium">{text}</Text>}
    </Pressable>
  )
})

const $container: ViewStyle = {
  justifyContent: "center",
}

const $text: TextStyle = {
  fontFamily: typography.primary.normal,
  fontSize: 14,
  color: colors.palette.primary500,
}
