import { Instance, SnapshotOut, types } from "mobx-state-tree"
import { InvoiceStoreModel } from "./InvoiceStore"
import { ReceiptStoreModel } from "./ReceiptStore"
import { LocationStoreModel } from "./LocationStore"
import { StoreStoreModel } from "./StoreStore"
import { DelnoteStoreModel } from "./DelnoteStore"
import { AuthenticationStoreModel } from "./AuthenticationStore" // @demo remove-current-line
import { EpisodeStoreModel } from "./EpisodeStore" // @demo remove-current-line

/**
 * A RootStore model.
 */
export const RootStoreModel = types.model("RootStore").props({
  invoiceStore: types.optional(InvoiceStoreModel, {} as any),
  receiptStore: types.optional(ReceiptStoreModel, {} as any),
  locationStore: types.optional(LocationStoreModel, {} as any),
  storeStore: types.optional(StoreStoreModel, {} as any),
  delnoteStore: types.optional(DelnoteStoreModel, {} as any),
  authenticationStore: types.optional(AuthenticationStoreModel, {}), // @demo remove-current-line
  episodeStore: types.optional(EpisodeStoreModel, {}), // @demo remove-current-line
})

/**
 * The RootStore instance.
 */
export interface RootStore extends Instance<typeof RootStoreModel> {}
/**
 * The data of a RootStore.
 */
export interface RootStoreSnapshot extends SnapshotOut<typeof RootStoreModel> {}
