import { Instance, SnapshotIn, SnapshotOut, types } from "mobx-state-tree"
import { withSetPropAction } from "./helpers/withSetPropAction"
import { Location } from "./Location"

/**
 * Model description here for TypeScript hints.
 */
export const StoreModel = types
  .model("Store")
  .props({
    id: types.identifier,
    store_name: "",
    store_location: 0,
    location: types.frozen<Location>()
  })
  .actions(withSetPropAction)
  .views((self) => ({})) // eslint-disable-line @typescript-eslint/no-unused-vars
  .actions((self) => ({})) // eslint-disable-line @typescript-eslint/no-unused-vars

export interface Store extends Instance<typeof StoreModel> {}
export interface StoreSnapshotOut extends SnapshotOut<typeof StoreModel> {}
export interface StoreSnapshotIn extends SnapshotIn<typeof StoreModel> {}
export const createStoreDefaultModel = () => types.optional(StoreModel, {})
