import { observer } from "mobx-react-lite"
import React, { FC, useEffect, useMemo, useRef, useState } from "react"
import { TextInput, TextStyle, ViewStyle } from "react-native"
import { Button, Icon, Screen, Text, TextField, TextFieldAccessoryProps } from "../components"
import { useStores } from "../models"
import { AppStackScreenProps } from "../navigators"
import { colors, spacing } from "../theme"
import { encryptAccessToken } from 'app/utils/security'
import _ from 'lodash'
import { path } from 'ramda'

interface BankingLoginScreenProps extends AppStackScreenProps<"Login"> {}

export const BankingLoginScreen: FC<BankingLoginScreenProps> = observer(function BankingLoginScreen(_props) {
  const authPasswordInput = useRef<TextInput>()

  const [isAuthPasswordHidden, setIsAuthPasswordHidden] = useState(true)
  const [isSubmitted, setIsSubmitted] = useState(false)
  const [attemptsCount, setAttemptsCount] = useState(0)
  const [responseMessage, setResponseMessage] = useState('')
  const {
    authenticationStore: {
      authEmail, setAuthEmail, setAuthToken, validationError, authPassword,
      userLoginInprogress, setUserLoginInprogress,
      setUserFullName, loginDoLogin, setUserEmail, setUserRole, setUserPrivileges,
      setAuthPassword
    },
  } = useStores()

  useEffect(() => {
    // Here is where you could fetch credentials from keychain or storage
    // and pre-fill the form fields.
    setAuthEmail("")
    setAuthPassword("")
  }, [])

  const error = isSubmitted ? validationError : ""

  async function login() {
    setIsSubmitted(true)
    setAttemptsCount(attemptsCount + 1)

    if (validationError) return

    // Make a request to your server to get an authentication token.
    // If successful, reset the fields and set the token.
    let response = await loginDoLogin()
    console.log('Response>>>>>>>>>>>>', response)
    if(response == null){
      console.log('gagal login')
      setAuthToken("")
      setResponseMessage('response null')
    } else {
      const errors = []
      if (!_.isEmpty(response.problem)) errors.push({ message: response.problem })
      response = {data: response}
      // detect error from body
      const status = parseInt(path(['data', 'data', 'login', 'status'], response) || 0)
      const errorbody = path(['data', 'data', 'login', 'error', 'message'], response) || []
      const errorbody2 = path(['data', 'data', 'login', 'error'], response) || []
      const errorBackend = path(['data', 'errors'], response) || []
      const errorResponse = path(['error'], response) || []

      // success data
      const token = path(['data', 'data', 'login', 'access_token'], response) || []
      const user = path(['data', 'data', 'login', 'user'], response) || {}
      const userPrivileges = path(['data', 'data', 'login', 'user_privileges'], response)
      const userMerchants = path(['data', 'data', 'login', 'user_merchants'], response)

      // find default merchant
      const merchant = _.find(userMerchants, { is_default: 'Y' }) || {}
      const merchantIdd = '1588133343427HsZOX'

      const role = path(['data', 'data', 'login', 'role'], response)
      const myprofile = user

      if (path(['data', 'responseMessage'], response) === 'FAILED_SYSTEM') {
        errors.push({ message: path(['data', 'responseDescription'], response) })
      }
      if (!_.isEmpty(errorResponse)) errors.push({ message: errorResponse })
      if (!_.isEmpty(errorbody)) errors.push({ message: errorbody })
      if (!_.isEmpty(errorbody2)) errors.push({ message: errorbody2 })
      if (!_.isEmpty(errorBackend)) { errors.push({ message: 'System error' }) }
    
      // success?
      // success?
      if (response.status === null) {
        // callErrorToast(response.problem, 'error')
        // return yield put(LoginActions.loginDoLoginFailed({ responseMessage: response.problem }))
      }
      if (_.isEmpty(errors) && !_.isEmpty(token)) {
        console.log('berhasil login', user)
        setUserFullName(user.full_name)
        setAuthPassword("")
        setAuthEmail("")
        setUserEmail(user.email)
        setUserRole(role)
        setUserPrivileges(userPrivileges)
        
        // let token = String(Date.now())
        // setSession({ [AppConfig.loginFlag]: true, [AppConfig.sessionToken]: token }, () => {})

        // We'll mock this with a fake token.
        setAuthToken(encryptAccessToken(token))
      } else {
        console.log('gagal login')
        setAuthToken("")
        const responseCode = status
        setResponseMessage(JSON.stringify(errors))
        // if (!isNullOrUndefined(errors[0].message)) { responseMessage = errors[0].message } else { responseMessage = errors[0] }
        // callToastr(`Ada Kesalahan. ${responseMessage}`,'error')
        // return yield put(LoginActions.loginDoLoginFailed({ responseCode, responseMessage }))
      }
    }
    setIsSubmitted(false)
  }

  const PasswordRightAccessory = useMemo(
    () =>
      function PasswordRightAccessory(props: TextFieldAccessoryProps) {
        return (
          <Icon
            icon={isAuthPasswordHidden ? "view" : "hidden"}
            color={colors.palette.neutral800}
            containerStyle={props.style}
            size={20}
            onPress={() => setIsAuthPasswordHidden(!isAuthPasswordHidden)}
          />
        )
      },
    [isAuthPasswordHidden],
  )

  useEffect(() => {
    return () => {
      setAuthPassword("")
      setAuthEmail("")
    }
  }, [])

  return (
    <Screen
      preset="auto"
      contentContainerStyle={$screenContentContainer}
      safeAreaEdges={["top", "bottom"]}
    >
      <Text testID="login-heading" tx="bankingLoginScreen.signIn" preset="heading" style={$signIn} />
      {/* <Text tx="loginScreen.enterDetails" preset="subheading" style={$enterDetails} /> */}
      {/* {attemptsCount > 2 && <Text tx="loginScreen.hint" size="sm" weight="light" style={$hint} />} */}
      {!!responseMessage && <Text text={responseMessage} size="sm" weight="light" style={$hint} />}

      <TextField
        value={authEmail}
        onChangeText={setAuthEmail}
        containerStyle={$textField}
        autoCapitalize="none"
        autoComplete="email"
        autoCorrect={false}
        keyboardType="email-address"
        labelTx="loginScreen.emailFieldLabel"
        placeholderTx="loginScreen.emailFieldPlaceholder"
        helper={error}
        status={error ? "error" : undefined}
        onSubmitEditing={() => authPasswordInput.current?.focus()}
      />

      <TextField
        ref={authPasswordInput}
        value={authPassword}
        onChangeText={setAuthPassword}
        containerStyle={$textField}
        autoCapitalize="none"
        autoComplete="password"
        autoCorrect={false}
        secureTextEntry={isAuthPasswordHidden}
        labelTx="loginScreen.passwordFieldLabel"
        placeholderTx="loginScreen.passwordFieldPlaceholder"
        onSubmitEditing={login}
        RightAccessory={PasswordRightAccessory}
      />
      {!userLoginInprogress && <Button
        testID="login-button"
        tx="loginScreen.tapToSignIn"
        style={$tapButton}
        preset="reversed"
        onPress={login}
      />}
      {userLoginInprogress && <Button
        disabled
        testID="login-button"
        text="inprogress..."
        style={$tapButton}
        preset="reversed"
        onPress={login}
      />}
    </Screen>
  )
})

const $screenContentContainer: ViewStyle = {
  paddingVertical: spacing.xxl,
  paddingHorizontal: spacing.lg,
}

const $signIn: TextStyle = {
  marginBottom: spacing.sm,
}

const $enterDetails: TextStyle = {
  marginBottom: spacing.lg,
}

const $hint: TextStyle = {
  color: colors.tint,
  marginBottom: spacing.md,
}

const $textField: ViewStyle = {
  marginBottom: spacing.lg,
}

const $tapButton: ViewStyle = {
  marginTop: spacing.xs,
}

