export const decryptAccessToken = (encryptedAccessToken) => {
    // check if encryptedAccessToken is empty
    if (!encryptedAccessToken) return ''
  
      // if (typeof (AES.decrypt) !== 'function') AES = require('crypto-js/aes')
      // if (EncUtf8 === null) EncUtf8 = require('crypto-js/enc-utf8')
      // var bytes = AES.decrypt(encryptedAccessToken, 'prismalink2019')
      // var decryptedData = bytes.toString(EncUtf8)
      // return decryptedData
      var bytes  = CryptoJS.AES.decrypt(encryptedAccessToken, 'prismalink2019');
      var plaintext = bytes.toString(CryptoJS.enc.Utf8);
      return plaintext
  }