import { Instance, SnapshotIn, SnapshotOut, types } from "mobx-state-tree"
import { withSetPropAction } from "./helpers/withSetPropAction"
import { Store } from "./Store"
import { Invoice } from "./Invoice"
import { Receipt } from "./Receipt"

/**
 * Model description here for TypeScript hints.
 */
export const DelnoteModel = types
  .model("Delnote")
  .props({
    id: types.identifier,
    nomor_surat: "",
    store_id: "",
    store: types.frozen<Store>(),
    invoice: types.frozen<Invoice>(),
    receipt: types.frozen<Receipt>(),
    quantity: types.number,
    delivery_year: types.number,
    delivery_month: types.number,
    delivery_date: types.number,
    total_case: types.number,
    price: "",
    created_dt: "",
    updated_dt: "",
    created_by: "",
    // updated_by: "",
    version: types.number,
  })
  .actions(withSetPropAction)
  .views((self) => ({
    get isInvoiceCreated() {
      return self.invoice?.nomor_invoice !== undefined
    },
    
    get parsedTitleAndSubtitle() {
      return { title: self.nomor_surat?.trim(), subtitle: self.store.store_name?.trim() }
    },
    get storeLocationName() {
      return self.store.location.location_name
    },
    get deliveryYearMonth() {
      // pad month with 0
      let delivery_month = `${self.delivery_month < 10 ? '0'+self.delivery_month:self.delivery_month}`
      let delivery_date = `${self.delivery_date < 10 ? '0'+self.delivery_date:self.delivery_date}`
      return `${self.delivery_year}-${delivery_month}-${delivery_date}`
    }
  })) // eslint-disable-line @typescript-eslint/no-unused-vars
  .actions((self) => ({})) // eslint-disable-line @typescript-eslint/no-unused-vars

export interface Delnote extends Instance<typeof DelnoteModel> {}
export interface DelnoteSnapshotOut extends SnapshotOut<typeof DelnoteModel> {}
export interface DelnoteSnapshotIn extends SnapshotIn<typeof DelnoteModel> {}
export const createDelnoteDefaultModel = () => types.optional(DelnoteModel, {})
