import React, { FC, ComponentType, forwardRef, Ref, ReactElement, useRef, useState, useEffect } from 'react';
import {
  FlatList,
  StyleSheet,
//   Text,
  TouchableOpacity,
  Modal,
  View,
  TextInput,
  TextInputProps,
  TextStyle,
  StyleProp,
  ViewStyle,
  ScrollView,
  Platform
} from 'react-native';
import { Icon } from 'app/components'
import { Text, TextProps } from "./Text"
import { colors, spacing, typography } from "app/theme"
import { useStores } from "app/models"
import { decryptAccessToken } from "app/utils/security"
// import { Icon } from 'react-native-elements';

interface TextFieldAccessoryProps {
    style: StyleProp<any>
    status: DropdownProps["status"]
    multiline: boolean
    editable: boolean
  }
export interface DropdownProps extends Omit<TextInputProps, "ref"> {
    onSelect: (item: { label: string; value: string }) => void;
    data: Array<{ label: string; value: string }>;
    status?: "error" | "disabled"
    defaultValue?: TextProps["text"]
    label?: TextProps["text"]
    labelTx?: TextProps["tx"]
    labelTxOptions?: TextProps["txOptions"]
    LabelTextProps?: TextProps
    helper?: TextProps["text"]
    helperTx?: TextProps["tx"]
    helperTxOptions?: TextProps["txOptions"]
    HelperTextProps?: TextProps
    placeholder?: TextProps["text"]
    placeholderTx?: TextProps["tx"]
    placeholderTxOptions?: TextProps["txOptions"]
    style?: StyleProp<TextStyle>
    containerStyle?: StyleProp<ViewStyle>
    inputWrapperStyle?: StyleProp<ViewStyle>
    RightAccessory?: ComponentType<TextFieldAccessoryProps>
    LeftAccessory?: ComponentType<TextFieldAccessoryProps>
}

export const Dropdown = forwardRef(function Dropdown(props: DropdownProps, ref: Ref<TextInput>) {
// export const Dropdown: FC<DropdownProps> = ({
//     label, data, onSelect,
//     containerStyle: $containerStyleOverride,
// }) => {
  const {
    defaultValue,
    data, onSelect,
    labelTx,
    label,
    labelTxOptions,
    placeholderTx,
    placeholder,
    placeholderTxOptions,
    helper,
    helperTx,
    helperTxOptions,
    status,
    RightAccessory,
    LeftAccessory,
    HelperTextProps,
    LabelTextProps,
    style: $inputStyleOverride,
    containerStyle: $containerStyleOverride,
    inputWrapperStyle: $inputWrapperStyleOverride,
    ...TextInputProps
  } = props
  const DropdownButton = useRef();
  const [visible, setVisible] = useState(false);
  const [selected, setSelected] = useState(undefined);
  const [dropdownTop, setDropdownTop] = useState(0);
  const disabled = TextInputProps.editable === false || status === "disabled"

  const $containerStyles = [$containerStyleOverride]
  const $labelStyles = [$labelStyle, LabelTextProps?.style]
  const $inputWrapperStyles = [
    $inputWrapperStyle,
    status === "error" && { borderColor: colors.error },
    TextInputProps.multiline && { minHeight: 112 },
    LeftAccessory && { paddingStart: 0 },
    RightAccessory && { paddingEnd: 0 },
    $inputWrapperStyleOverride,
  ]

  const toggleDropdown = (): void => {
    visible ? setVisible(false) : openDropdown();
  };

  const openDropdown = (): void => {
    DropdownButton.current.measure((_fx: number, _fy: number, _w: number, h: number, _px: number, py: number) => {
      setDropdownTop(py + h);
    });
    setVisible(true);
  };

  const onItemPress = (item: any): void => {
    // check if defaultValue is undefined
    if (defaultValue === undefined) setSelected(item);
    onSelect(item);
    setVisible(false);
  };

  const { locationStore, delnoteStore: deliverynoteStore, authenticationStore: { authToken } } = useStores()

  

  const renderItem = ({ item }: any): ReactElement<any, any> => (
    <TouchableOpacity style={styles.item} onPress={() => onItemPress(item)}>
      <Text>{item.label}</Text>
    </TouchableOpacity>
  );

  // const renderDropdown = (): ReactElement<any, any> => {
  //   return (
  //     // <Modal visible={visible} transparent animationType="none">
  //       <Modal visible={visible} animationType="none">
  //           {/* <ScrollView
  //             style={{ backgroundColor: 'red' }}
  //           > */}
          
  //             <View style={styles.centeredView}>
  //                 <TouchableOpacity
  //                   style={{ backgroundColor: 'blue', width: '100%', height: '100%' }}
  //                   onPress={() => setVisible(false)}
  //                 >
  //                     <View style={styles.modalView}>
  //             {/* 
  //                     <View style={[styles.dropdown]}> */}
                      
  //                       <FlatList
  //                         data={data}
  //                         renderItem={renderItem}
  //                         keyExtractor={(item, index) => index.toString()}
  //                         scrollEnabled={true}
  //                       />
                      
  //                     {/* </View>
  //               */}
  //                     </View>
  //                 </TouchableOpacity>
  //                   </View>
  //           {/* </ScrollView> */}
          
  //       </Modal>
  //   );
  // };

  const ModalNative = () => (
    <Modal transparent visible={visible} animationType="none">
      <View style={styles.centeredView}>
        <TouchableOpacity
          style={{ width: '100%', height: '100%' }}
          onPress={() => setVisible(false)}
        >
          <View style={styles.modalView}>
          <FlatList
              data={data}
              renderItem={renderItem}
              keyExtractor={(item, index) => index.toString()}
              scrollEnabled={true}
            />
          </View>
        </TouchableOpacity>
      </View>
    </Modal>
  )
  const ModalWeb = () => (
    <Modal transparent visible={visible} animationType="none">
      <ScrollView>
        <View style={styles.centeredView}>
          <TouchableOpacity
            style={{ width: '100%', height: '100%' }}
            onPress={() => setVisible(false)}
          >
            <View style={styles.modalView}>
            <FlatList
                data={data}
                renderItem={renderItem}
                keyExtractor={(item, index) => index.toString()}
                scrollEnabled={true}
              />
            </View>
          </TouchableOpacity>
        </View>
      </ScrollView>
    </Modal>
  )

  function getButtonLabel() {
    let findLabel = (defaultValue ? data.find(o => o.value === defaultValue) || {} : {}).label
    let label = findLabel || selected?.label
    || placeholder
    // console.log('dropdown label:', label, ' defaultValue:', defaultValue, ' placeholder:', placeholder, ' selected:', selected?.label, ' findLabel:', findLabel)
    return label
  }

  return (
      <TouchableOpacity
        ref={DropdownButton}
        activeOpacity={1}
        style={$containerStyles}
        onPress={toggleDropdown}
        accessibilityState={{ disabled }}
      >
          {!!(label || labelTx) && (
          <Text
            preset="formLabel"
            text={label}
            tx={labelTx}
            txOptions={labelTxOptions}
            {...LabelTextProps}
            style={$labelStyles}
          />
        )}
        {Platform.OS === 'web' ? <ModalWeb /> : <ModalNative />}
        <View style={[$inputWrapperStyles]}>
          {!!LeftAccessory && (
            <LeftAccessory
              style={$leftAccessoryStyle}
              status={status}
              editable={!disabled}
              multiline={TextInputProps.multiline}
            />
          )}
          <Text style={styles.buttonText}>
              {/* {(!!selected && selected.label) || placeholder} */}
              {
                getButtonLabel()
              }
          </Text>
          {!!RightAccessory && (
            <RightAccessory
              style={$rightAccessoryStyle}
              status={status}
              editable={!disabled}
              multiline={TextInputProps.multiline}
            />
          )}
        </View>
      </TouchableOpacity>
  );
});
const $labelStyle: TextStyle = {
    marginBottom: spacing.extraSmall,
  }
  const $inputWrapperStyle: ViewStyle = {
    flexDirection: "row",
    alignItems: "flex-start",
    borderWidth: 1,
    borderRadius: 4,
    backgroundColor: colors.palette.neutral200,
    borderColor: colors.palette.neutral400,
    overflow: "hidden",
    height: 40
  }
const styles = StyleSheet.create({
  centeredView: {
    // flex: 1,
    justifyContent: 'center',
    alignItems: 'center',
    marginTop: 22,
  },
  modalView: {
    margin: 20,
    backgroundColor: 'white',
    borderRadius: 20,
    padding: 35,
  //   alignItems: 'center',
    shadowColor: '#000',
    shadowOffset: {
      width: 0,
      height: 2,
    },
    shadowOpacity: 0.25,
    shadowRadius: 4,
    elevation: 5,
  },
  button: {
    flexDirection: 'row',
    alignItems: 'center',
    backgroundColor: '#efefef',
    height: 50,
    zIndex: 1,
  },
  buttonText: {
    flex: 1,
    // textAlign: 'center',
    height: 40,
    paddingHorizontal: 10,
    paddingVertical: 10,
  },
  icon: {
    marginRight: 10,
  },
  dropdown: {
    // position: 'absolute',
    backgroundColor: '#fff',
    width: '100%',
    shadowColor: '#000000',
    shadowRadius: 4,
    shadowOffset: { height: 4, width: 0 },
    shadowOpacity: 0.5,
  },
  overlay: {
    flex: 1,
    width: '100%',
    // height: '100%',
    backgroundColor: 'yellow'
  },
  item: {
    paddingHorizontal: 10,
    paddingVertical: 10,
    borderBottomWidth: 1,
  },
});
const $rightAccessoryStyle: ViewStyle = {
    marginEnd: spacing.extraSmall,
    height: 40,
    justifyContent: "center",
    alignItems: "center",
  }
const $leftAccessoryStyle: ViewStyle = {
    marginStart: spacing.extraSmall,
    height: 40,
    justifyContent: "center",
    alignItems: "center",
  }