import { Instance, SnapshotIn, SnapshotOut, types } from "mobx-state-tree"
import { withSetPropAction } from "./helpers/withSetPropAction"

/**
 * Model description here for TypeScript hints.
 */
export const ReceiptModel = types
  .model("Receipt")
  .props({
    id: types.identifier,
    nomor_kwitansi: "",
  })
  .actions(withSetPropAction)
  .views((self) => ({})) // eslint-disable-line @typescript-eslint/no-unused-vars
  .actions((self) => ({})) // eslint-disable-line @typescript-eslint/no-unused-vars

export interface Receipt extends Instance<typeof ReceiptModel> {}
export interface ReceiptSnapshotOut extends SnapshotOut<typeof ReceiptModel> {}
export interface ReceiptSnapshotIn extends SnapshotIn<typeof ReceiptModel> {}
export const createReceiptDefaultModel = () => types.optional(ReceiptModel, {})
